<template>
  <div>
     <Xxtop mes="我的手记"></Xxtop>
     <div class="mysj-main" v-for="(sj,a) in myshouji" :key="a" @click="gomysj(sj)">
        <div class="sjimg">
          <img src="/img/wode/shoujitouxiang.png" alt="">
        </div>
        <div>{{sj.kanguonum}}</div>
        <div class="sj-main">
            <p>{{sj.tit}}</p>
        </div>

     </div>
  </div>
</template>

<script>
  import Xxtop from "../../components/xx-top.vue"

  export default {
    name: 'myshouji',
    components: {
      Xxtop
  },
  data(){
    return{
      myshouji:[]
    }
  },
  created() {
      this.$axios.get("/json/shouji.json").then(res=>{
        this.myshouji = res.data
      })
  },
  methods:{
    gomysj(sj){
      this.$axios.get("/json/chihujianjie.json").then(res=>{
        for(var i=0;i<res.data.length;i++){
            if(sj.id == res.data[i].id){
                var go = true
            }
        }
        if(go){
          this.$router.push({
            path:'/chihujj',
            query:{
                id: sj.id
            }
          })
          
        }else{
          alert("暂无数据！")
        }
      })
    }
  }
  }
</script>

<style scoped>
  .mysj-main{
    height: 140px;
    padding-left: 30px;
    padding-right: 30px;
    width: calc(100% - 60px);
    border-bottom: 1px solid #eeeeee;
    overflow: hidden;
  }
  .mysj-main>div{
    float: left;
  }
  .mysj-main .sjimg{
    height: 100px;
    width: 100px;
    border-radius: 50%;
    overflow: hidden;
    margin-top: 20px;
  }
  .mysj-main .sjimg>img{
    position: relative;
    top: -35px;
  }

  .mysj-main .sj-main{
    margin-left: 20px;
    width: calc(100% - 120px);
    position: relative;
    top: -32px;
  }
  .mysj-main .sj-main>p{
    font-size: 30px;
    color: #1b1b1b;
    margin-top: 20px;
    line-height: 50px;
  }
  .mysj-main>div:nth-child(2){
    position: relative;
    float: right;
    top: 82px;
    margin-right: 25px;
    font-size: 24px;
    color: #929292;
  }
</style>
